import Go from './core/GoCore'
import { GoComponentConfig } from './types'

// 创建图表
const createComponent = (type: string, op?: GoComponentConfig) => {
  if (!type) {
    console.log('!请传入正确的组件类型')
    return
  }
  return new Go(type, op).runder()
}
const analysis = (list: GoComponentConfig): void => {
  list.forEach(item => {
    createComponent(item.type, item)
  })
}
export { createComponent, analysis }
