import { VuexServer } from '../vuex/vuex'
import { AxiosPromise } from '../types'
class LoadComs extends VuexServer {
  public type: string
  public name: string
  public cid: string
  public tagName: string
  static baseUrl: string = 'http://172.16.9.143:8090/subassembly/js/'
  public storeModule: string = 'GoCom'
  public com: any
  public tmp: any
  constructor(type: string) {
    super()
    this.type = type
  }

  static setBaseUrl(url: string): void {
    LoadComs.baseUrl = url
  }
  public _loadComponent(dev?: boolean, callback?: () => void): void {
    // 动态加载组件
    let path = this.getComponentPath()
    let _this = this
    if (dev) {
      _this.com = window[_this.tagName]
      _this.tmp = window[_this.tagName + '-Tmp']
      _this.callMutation('setSelect', _this.cid)
      _this.callMutation('addComs', _this)
      _this.callAction('addMenuItem', _this)
      _this.callMutation('addBaseComs', _this)
      return
    }
    async function load(callback: () => void) {
      await _this._loadScript(path[0])
      await _this._loadScript(path[1])
      _this.com = window[_this.tagName]
      _this.tmp = window[_this.tagName + '-Tmp']
      _this.callMutation('setSelect', _this.cid)
      _this.callMutation('addComs', _this)
      _this.callAction('addMenuItem', _this)
      _this.callMutation('addBaseComs', _this)
      if (callback) {
        callback()
      }
    }
    if (_this.callState('baseComs').filter(v => v.type === this.type).length > 0) {
      _this.com = window[_this.tagName]
      _this.tmp = window[_this.tagName + '-Tmp']
      _this.callMutation('setSelect', _this.cid)
      _this.callMutation('addComs', _this)
      _this.callAction('addMenuItem', _this)
    } else {
      load(callback)
    }
  }
  // 动态加载js 文件
  public _loadScript(src: string, attrs?: any, parentNode?: any): AxiosPromise {
    return new Promise((resolve, reject) => {
      const script = document.createElement('script')
      script.async = true
      script.src = src
      // for (const [k, v] of Object.entries(attrs || {})) {
      //   script.setAttribute(k, v);
      // }
      script.onload = () => {
        script.onerror = script.onload = null
        resolve(script)
      }
      script.onerror = () => {
        script.onerror = script.onload = null
        reject(new Error(`Failed to load ${src}`))
      }
      const node = parentNode || document.head || document.getElementsByTagName('head')[0]
      node.appendChild(script)
    })
  }
  public getComponentPath(): string[] {
    let basePath: string = this.type.replace(/\./g, '/')
    let name: string = this.type.replace(/\./g, '-')
    let path1 = LoadComs.baseUrl + basePath + '/' + name + '.js'
    let path2 = LoadComs.baseUrl + basePath + '/' + name + '-Tmp.js'
    this.tagName = name
    return [path1, path2]
  }
}

export { LoadComs }
