import { LoadComs } from './LoadScript'
import { BaseData, GoComponentConfig } from '../types'
let appStore: object = {}
class Go extends LoadComs {
  public type: string
  public name: string
  public eventNum: number = 0
  public actionSource: number | string = 0
  // public isSelect: boolean
  /**
   * name
   */
  public cid: string
  public dev: boolean = false
  public isShow: boolean = true
  public isLock: boolean = false
  public typeId: number | string
  public z: number
  public baseData: BaseData = {
    left: 0,
    top: 0,
    width: 300,
    height: 200
  }
  public actions = new Set()
  // : {
  //   source: {}
  //   implement: {}
  // }
  public optionData: {} = {}
  public sourceData: {} = {}
  public functionData: any[]
  constructor(type?: string, op?: GoComponentConfig) {
    super(type)
    op ? (this.name = op.name) : (this.name = this.type)
    op && op.baseData && (this.baseData = op.baseData)
    op && op.optionData && (this.optionData = op.optionData)
    op && op.sourceData && (this.sourceData = op.sourceData)
    op && op.dev && (this.dev = op.dev)
    op && op.cid ? (this.cid = op.cid) : (this.cid = this.genID())
  }
  runder() {
    this._loadComponent(this.dev)
  }
  // 得到数据仓库
  getAppStore() {
    return appStore
  }
  // 运行时动态设置数据仓库
  static _setAppStore(_appStore: object) {
    appStore = _appStore
  }
  public genID(): string {
    return (
      Math.random()
        .toString()
        .substr(3, 10) + Date.now()
    ).toString()
  }
  // 获取当前层级
  public get zIndex(): number {
    let index = this.callState('menuList').findIndex(v => v.cid === this.cid)
    return index
  }
  // 获取当前层级
  public set zIndex(val: number) {
    this.z = val
    // this.callMutation("menuList").findIndex((v) => v.cid == this.cid);
  }
  // 获取当前层级
  // public set eventNum(val: number) {
  //   this.callMutation("menuList").findIndex((v) => v.cid == this.cid);
  // }
  // 获取是否是选中状态
  public get isSelect(): boolean {
    return this.callState('selectd').includes(this.cid)
  }
  // public get functionData(): any[] {
  //   return this.callState('list').filter(v => v.cid === this.cid)[0].actionData
  // }
  // // 设置选中状态
  // public set functionData(val: any[]) {
  //   console.log(val)
  //   // this.callMutation("setSelect", this.cid);
  // }
  // 设置选中状态
  public set isSelect(val: boolean) {
    // this.callMutation("setSelect", this.cid);
  }
}

export default Go
