import { GoComponentConfig } from '../types'
function typeseting(list: Array<GoComponentConfig>, type: Number, op?: {}): Array<any> {
  let _list = list.map(v => v)
  let toplist = list.map(v => v)
  let leftlist = list.map(v => v)
  let bottomlist = list.map(v => v)
  let rightlist = list.map(v => v)
  let chlist = list.map(v => v)
  let cwlist = list.map(v => v)
  let wlist = list.map(v => v)
  toplist.sort((a, b) => a.baseData.top - b.baseData.top)
  leftlist.sort((a, b) => a.baseData.left - b.baseData.left)
  bottomlist.sort(
    (a, b) => a.baseData.top + a.baseData.height - (b.baseData.top + b.baseData.height)
  )
  rightlist.sort(
    (a, b) => a.baseData.left + a.baseData.width - (b.baseData.left + b.baseData.width)
  )
  chlist.sort(
    (a, b) => (a.baseData.top + a.baseData.height) / 2 - (b.baseData.top + b.baseData.height) / 2
  )
  cwlist.sort(
    (a, b) => (a.baseData.left + a.baseData.width) / 2 - (b.baseData.left + b.baseData.width) / 2
  )
  wlist.sort((a, b) => a.baseData.left - b.baseData.left)
  let lBox = wlist[0]
  let rBox = _list.sort((a, b) => {
    return b.baseData.left + b.baseData.width - a.baseData.left + a.baseData.width
  })[0]
  let tBox = _list.sort((a, b) => a.baseData.top - b.baseData.top)[0]
  let bBox = _list.sort(
    (a, b) => b.baseData.top + b.baseData.height - (a.baseData.top + a.baseData.height)
  )[0]

  switch (type) {
    case 0: // 左对齐
      _list.forEach(item => {
        item.baseData.left = lBox.baseData.left
      })
      break
    case 1: // 右对齐
      let r = rightlist[rightlist.length - 1].baseData.left + rightlist[0].baseData.width
      _list.forEach(item => {
        item.baseData.left = r - item.baseData.width
      })
      break
    case 2: // 水平剧中对齐
      let center =
        (leftlist[0].baseData.left +
          leftlist[leftlist.length - 1].baseData.left +
          leftlist[leftlist.length - 1].baseData.width) /
        2
      _list.forEach(item => {
        item.baseData.left = center - item.baseData.width / 2
      })
      break
    case 3: // 顶对齐
      _list.forEach(item => {
        item.baseData.top = tBox.baseData.top
      })
      break
    case 4: // 底对齐
      _list.forEach(item => {
        item.baseData.top = bBox.baseData.top + bBox.baseData.height - item.baseData.height
      })
      break
    case 5: // 垂直剧中对齐
      let hcenter =
        toplist[0].baseData.top +
        (toplist[0].baseData.top +
          bottomlist[bottomlist.length - 1].baseData.top +
          bottomlist[bottomlist.length - 1].baseData.height) /
          2
      let _center = _list[0].baseData.top + list[0].baseData.height / 2
      if (!_list.every(v => v.baseData.top + v.baseData.height / 2 === _center)) {
        _list.forEach(item => {
          // if (item.baseData.top !== hcenter) {
          item.baseData.top = hcenter - item.baseData.height / 2
          // }
        })
      }
      break
    case 6: // 按顶分布
      let num = toplist.length - 1
      let height = toplist[toplist.length - 1].baseData.top - toplist[0].baseData.top
      let step = height / num
      toplist.map((item, index) => {
        if (index > 0 && index < toplist.length - 1) {
          item.baseData.top = toplist[0].baseData.top + step * index
        }
      })
      _list = toplist
      break
    case 7: // 按底分布
      let dnum = bottomlist.length - 1
      let dheight = bottomlist[bottomlist.length - 1].baseData.top - bottomlist[0].baseData.top
      let dstep = dheight / dnum
      bottomlist.map((item, index) => {
        if (index > 0 && index < bottomlist.length - 1) {
          item.baseData.top =
            bottomlist[0].baseData.top +
            bottomlist[0].baseData.height +
            dstep * index -
            item.baseData.height
        }
      })

      _list = bottomlist
      break
    case 8: // 按左分布
      let lnum = leftlist.length - 1
      let lheight = leftlist[leftlist.length - 1].baseData.left - leftlist[0].baseData.left
      let lstep = lheight / lnum
      leftlist.map((item, index) => {
        if (index > 0 && index < leftlist.length - 1) {
          item.baseData.left = leftlist[0].baseData.left + lstep * index
        }
      })
      _list = leftlist
      break
    case 9: // 垂直剧中分布
      let cnum = chlist.length - 1
      let cheight = chlist[chlist.length - 1].baseData.top - chlist[0].baseData.top
      let cstep = cheight / cnum
      chlist.map((item, index) => {
        if (index > 0 && index < chlist.length - 1) {
          item.baseData.top =
            chlist[0].baseData.top +
            chlist[0].baseData.height / 2 +
            cstep * index -
            item.baseData.height / 2
        }
      })
      _list = chlist
      break
    case 10: // 按右分布
      let rnum = rightlist.length - 1
      let rheight =
        rightlist[rightlist.length - 1].baseData.left +
        rightlist[rightlist.length - 1].baseData.width -
        (rightlist[0].baseData.left + rightlist[0].baseData.width)
      let rstep = rheight / rnum
      rightlist.map((item, index) => {
        if (index > 0 && index < rightlist.length - 1) {
          item.baseData.left =
            rightlist[0].baseData.left +
            rightlist[0].baseData.width +
            rstep * index -
            item.baseData.width
        }
      })
      _list = rightlist
      break
    case 11: // 水平剧中分布
      let cwnum = chlist.length - 1
      let cwheight = chlist[chlist.length - 1].baseData.top - chlist[0].baseData.top
      let cwstep = cwheight / cwnum
      cwlist.map((item, index) => {
        if (index > 0 && index < chlist.length - 1) {
          item.baseData.left =
            chlist[0].baseData.left +
            chlist[0].baseData.width / 2 +
            cwstep * index -
            item.baseData.width / 2
        }
      })
      _list = cwlist
      break
    default:
      break
  }
  return _list
}

export default typeseting
