import Go from '../core/GoCore'
class RuntimeServer extends Go {
  constructor() {
    super()
  }
  // 设置数据仓库
  setAppStore(appStore: any) {
    appStore && Go.setAppStore(appStore)
  }
  setBaseUrl(url: string) {
    url && Go.setBaseUrl(url)
  }
}

const runtimeServer = new RuntimeServer()

export { runtimeServer }
